body {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  background-color: #f9f9f9;
}

header {
  background-color: transparent;
  color: black;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  nav ul {
    list-style: none;
    display: flex;
    gap: 1.5rem;
    padding: 0;
    margin-top: 0.5rem;

    li {
      cursor: pointer;
      font-weight: 500;
    }
  }
}

.main-content {
  text-align: center;
  padding: 4rem 2rem;

  h1 {
    font-size: 2rem;
  }

  p {
    font-size: 1.1rem;
    margin-top: 0.5rem;
  }
}

footer {
  background-color: transparent;
  text-align: center;
  padding: 1rem;
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  header nav ul {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }

  .main-content {
    padding: 2rem 1rem;

    h1 {
      font-size: 1.5rem;
    }

    p {
      font-size: 1rem;
    }
  }

  footer {
    font-size: 0.8rem;
  }

  #map {
    height: 300px !important;
  }
}
